import '../styles/Footer.css';
import '../styles/Home.css';
import { Link } from 'react-router-dom';
import image from '../images/analytics.jpg';
import logo from '../images/bottlogo.png';
import Menu from '../components/Menu';

function App() {
  document.title = 'Bott Digital - Criação de sites';
  return (
    <div className="App">

      <Menu />
      <div className="cabecalho" style={{ backgroundColor: "green", color: "black" }}>
        <img src={logo} className="App-logo" alt="logo" />
      </div>
      <div className="about" id="about">
        <div className="content">
          <h2>Manutenção</h2>
          <p>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Cras egestas fringilla nibh, sed mollis tortor gravida condimentum. Maecenas accumsan ullamcorper tortor, vitae dictum nisi maximus eget. Nunc ex diam, bibendum sit amet diam sed, vulputate pulvinar sem. Aliquam erat volutpat. Sed quis purus eget mi porta suscipit vel vel urna. Interdum et malesuada fames ac ante ipsum primis in faucibus. Morbi ac lobortis erat. Phasellus sed nisi massa. Suspendisse pharetra libero vel lacus pretium, in posuere elit iaculis. Ut sagittis nisi a consequat faucibus. Nunc ullamcorper nunc ac diam lacinia cursus. Nullam iaculis tortor ac nulla interdum, sit amet dignissim urna molestie.
        </p>
          <img src={image} className="image-content" alt="Criação de Websites." />
          <br></br>
          <br></br>

          <Link class="btn btn-secondary btn-read-more" to={'/'}> Voltar </Link>
        </div>
      </div>
    </div >
  );
}

export default App;
