import React, { Component } from 'react';
import '../styles/Services.css';
import { Link } from 'react-router-dom';
import analytics from '../images/analytics.jpg';
import sitecreate from '../images/sitecreate.jpg';
import seo from '../images/seo.jpg';



// https://react.semantic-ui.com/


class Services extends Component {

  render() {
    return (
      <div className="services" id="services">
        <h2 id="title-page">Serviços prestados</h2>
        <div className="c ">
          <div className="column-3" >
            <img src={sitecreate} className=" image-border-radius" alt="logo" />
            <h2>Manutenção</h2>
            <p>Lorem ipslum dori me ameno ameno dorime.</p>
            <br></br>
            <Link class="btn btn-secondary btn-read-more" to={'Criacao-site/'}> Ver mais </Link>
          </div>
          <div className="column-3" >
            <img src={seo} className="image-border-radius" alt="logo" />
            <h2>Substituição</h2>
            <p>Lorem ipslum dori me ameno ameno dorime.</p>
            <br></br>
            <Link class="btn btn-secondary btn-read-more" to={'Busca-organica/'}> Ver mais </Link>
          </div>

          <div className="column-3" >
            <img src={analytics} className="image-border-radius" alt="logo" />
            <h2>Instalação</h2>
            <p>Lorem ipslum dori me ameno ameno dorime.</p>
            <br></br>
            <Link class="btn btn-secondary btn-read-more" to={'Analytics/'}> Ver mais </Link>
          </div>
        </div>
      </div>

    );
  }
}

export default Services;