import '../styles/Footer.css';
import '../styles/Home.css';
import '../styles/Home.css';
import { Link } from 'react-router-dom';
import sitecreate from '../images/sitecreate.jpg';
import logo from '../images/bottlogo.png';
import gerador1 from '../images/gerador1.jpg';
import gerador2 from '../images/gerador2.jpg';
import gerador3 from '../images/gerador3.jpg';
import gerador4 from '../images/gerador4.png';
import gerador5 from '../images/gerador5.jpg';
import gerador6 from '../images/gerador6.png';
import gerador7 from '../images/gerador7.jpg';
import gerador8 from '../images/gerador8.jpg';

import Menu from '../components/Menu';


function App() {
  document.title = 'Bott Digital - Criação de sites';
  return (
    <div className="App">
      <Menu />
      <div className="cabecalho" style={{ backgroundColor: "green", color: "black" }}>
        <img src={logo} className="App-logo" alt="logo" />
      </div>

      <div className="services" style={{ backgroundColor: "white", color: "black" }} id="services">
        <h2 id="title-page">Nossos geradores</h2>
        <div className="row">
          <div className="column-4" >
            <img src={gerador1} className=" image-border-radius" alt="logo" />
            <h2>Gerador 1</h2>
          </div>
          <div className="column-4" >
            <img src={gerador2} className="image-border-radius" alt="logo" />
            <h2>Gerador 2</h2>
          </div>

          <div className="column-4" >
            <img src={gerador3} className="image-border-radius" alt="logo" />
            <h2>Gerador 3</h2>
          </div>
          <div className="column-4" >
            <img src={gerador4} className="image-border-radius" alt="logo" />
            <h2>Gerador 4</h2>
          </div>
        </div>
        <br></br>
        <div className="c">
          <div className="column-4" >
            <img src={gerador5} className=" image-border-radius" alt="logo" />
            <h2>Gerador 5</h2>
          </div>
          <div className="column-4" >
            <img src={gerador6} className="image-border-radius" alt="logo" />
            <h2>Gerador 6</h2>
          </div>

          <div className="column-4" >
            <img src={gerador7} className="image-border-radius" alt="logo" />
            <h2>Gerador 7</h2>
          </div>
          <div className="column-4" >
            <img src={gerador8} className="image-border-radius" alt="logo" />
            <h2>Gerador 8</h2>
          </div>
        </div>
      </div>
      <Link class="btn btn-primary btn-read-more" to={'/'}> Voltar </Link>
      <br></br>
      <br></br>
      <br></br>
      <br></br>

    </div>

  );
}

export default App;
