import React, { Component } from 'react';
import '../styles/About.css';
import image from '../images/bottlogo2.png'

// https://react.semantic-ui.com/


class About extends Component {

  render() {
    return (
      <div className="about" id="about">
        <h2>Quem somos</h2>
        <div className="row">
          <div className="column-2">
            <p style={{ fontSize: "21.4px" }}>
              
A RF Refrigeração é uma empresa especializada em refrigeração comercial, oferecendo soluções completas e eficientes para atender às necessidades de estabelecimentos comerciais. Com uma equipe altamente qualificada e experiente, fornecemos serviços de instalação, manutenção e reparo de sistemas de refrigeração, garantindo o perfeito funcionamento de equipamentos como câmaras frigoríficas, balcões refrigerados e unidades de refrigeração em geral. 
            </p>
          </div>
          <div className="column-2">
            <img src={image} className="about-image" alt="Criação de Websites." />
          </div>
        </div >
      </div>
    );
  }
}

export default About;